import { useState } from 'react';
import { useRouter } from 'next/router';
import Link from "next/link";

export default function Navbar() {
    const [isOpen, setIsOpen] = useState(false);
    const router = useRouter();

    return (
        React.createElement('div', null, 
            React.createElement('div', { className: "w-full fixed top-0 bg-slate-950 flex justify-between px-11 py-6 font-sans shadow-lg" },
                React.createElement('div', { className: "text-3xl text-orange-500 font-bold cursor-pointer" }, 'PORTFOLIO'),
                React.createElement('div', { className: "flex items-center" },
                    React.createElement('button', { 
                        className: "lg:hidden text-white", 
                        onClick: () => setIsOpen(!isOpen) 
                    },
                        React.createElement('svg', { xmlns: "http://www.w3.org/2000/svg", fill: "none", viewBox: "0 0 24 24", stroke: "currentColor", className: "w-6 h-6" },
                            React.createElement('path', { strokeLinecap: "round", strokeLinejoin: "round", strokeWidth: "2", d: "M4 6h16M4 12h16M4 18h16" })
                        )
                    ),
                    React.createElement('div', { className: `lg:flex gap-8 ${isOpen ? 'block' : 'hidden'} lg:block` },
                        React.createElement(Link, { 
                            className: `text-zinc-300 text-xl font-bold ${router.pathname === '/' ? 'border-b-4 border-orange-500' : ''} hover:border-b-4 hover:border-orange-500`, 
                            href: "/" 
                        }, 'Home'),
                        React.createElement(Link, { 
                            className: `text-zinc-300 text-xl font-bold ${router.pathname === '/about' ? 'border-b-4 border-orange-500' : ''} hover:border-b-4 hover:border-orange-500`, 
                            href: "/about" 
                        }, 'About'),
                        React.createElement(Link, { 
                            className: `text-zinc-300 text-xl font-bold ${router.pathname === '/skill' ? 'border-b-4 border-orange-500' : ''} hover:border-b-4 hover:border-orange-500`, 
                            href: "/skill" 
                        }, 'Skill'),
                        React.createElement(Link, { 
                            className: `text-zinc-300 text-xl font-bold ${router.pathname === '/projects' ? 'border-b-4 border-orange-500' : ''} hover:border-b-4 hover:border-orange-500`, 
                            href: "/projects" 
                        }, 'Project'),
                        React.createElement(Link, { 
                            className: `text-zinc-300 text-xl font-bold ${router.pathname === '/contact' ? 'border-b-4 border-orange-500' : ''} hover:border-b-4 hover:border-orange-500`, 
                            href: "/contact" 
                        }, 'Contact')
                    )
                )
            )
        )
    );
}
